@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
* {
  box-sizing: border-box;
  padding: 0rem;
  margin: 0rem;
}

a {
  text-decoration: none;
  color: blueviolet;
}

body {
  margin: 0;
  font-family: "Indie Flower", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 21px;
  background-color: #282c34;
  color: aliceblue;
  max-width: 1200px;
  margin: 0 auto;
}
body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0}@keyframes move{0%{transform:translate(0%, 0);color:blue}50%{text-shadow:0 25px 50px #000}100%{transform:translate(30%, 0)}}@keyframes myAnimation{0%{color:red}25%{color:purple;transform:translateX(5%)}50%{color:blue;transform:translateY(5%)}75%{color:green;transform:translateX(7%)}100%{color:red;transform:translateY(7%)}}.App{padding-left:3em;text-align:left}.won-card{background:linear-gradient(to right, rgba(39, 39, 39, 0.8), rgba(39, 39, 39, 0.2));background-color:#4da375}.lost-card{background:linear-gradient(to right, rgba(39, 39, 39, 0.8), rgba(39, 39, 39, 0.2));background-color:#a34d5d}
.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .vs{font-size:40px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}.MatchDetailCard h1:hover{font-size:60px}
.YearSelector{text-align:right;list-style-type:none}.YearSelector li{font-size:30px;margin-top:10px;margin-bottom:10px}
.MatchPage{margin-top:40px;display:grid;grid-template-columns:1fr 5fr 1fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}
.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}.MatchSmallCard .vs{font-size:40px}.MatchSmallCard .additional-detail{text-align:right}.MatchSmallCard .additional-detail>p{margin-bottom:20px}.MatchSmallCard h1:hover{font-size:50px}
*{box-sizing:border-box;margin:0;padding:0}@keyframes move{0%{transform:translate(0%, 0);color:blue}50%{text-shadow:0 25px 50px #000}100%{transform:translate(30%, 0)}}@keyframes myAnimation{0%{color:red}25%{color:purple;transform:translateX(5%)}50%{color:blue;transform:translateY(5%)}75%{color:green;transform:translateX(7%)}100%{color:red;transform:translateY(7%)}}.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr .5fr;grid-template-rows:.5fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:20px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:start;font-size:3rem}.TeamPage .team-name-section :hover{animation:myAnimation .4s ease-in-out infinite;animation-direction:alternate}.TeamPage .match-detail-section{grid-column:span 5}.TeamPage .match-detail-section .words span{font-size:2rem}.TeamPage .match-detail-section :hover .body2{background:#fff;display:flex;flex-direction:column;height:10vh;justify-content:start;align-items:start}.TeamPage .match-detail-section :hover .words{color:red;font-size:0;line-height:1.5}.TeamPage .match-detail-section :hover .words span{display:inline-block;animation:move .4s ease-in-out infinite;animation-direction:alternate}.TeamPage .Win-loss-section{text-align:right;margin-top:10px}.TeamPage .Wins{display:inline-block;transition:all .7s ease-in-out}.TeamPage .Wins:hover{color:blue}.TeamPage .Wins.touched{transform:rotate(180deg)}.TeamPage .Losses{display:inline-block;transition:all .7s ease-in-out}.TeamPage .Losses:hover{color:red}.TeamPage .Losses.touched{transform:rotate(-180deg)}.TeamPage .more-link{align-self:center;justify-self:center;font-size:3.5rem;grid-column:span 2}.TeamPage .more-link:hover{font-size:4.5rem}
.TeamTile{height:170px;text-align:center;border:1px solid #fff;padding:20px}.TeamTile h1:hover{font-size:50px}
*{box-sizing:border-box;margin:0;padding:0}@keyframes move{0%{transform:translate(0%, 0);color:blue}50%{text-shadow:0 25px 50px #000}100%{transform:translate(30%, 0)}}@keyframes myAnimation{0%{color:red}25%{color:purple;transform:translateX(5%)}50%{color:blue;transform:translateY(5%)}75%{color:green;transform:translateX(7%)}100%{color:red;transform:translateY(7%)}}.HomePage{margin-bottom:20px}.HomePage .header-section :hover{animation:myAnimation .8s ease-in-out infinite;animation-direction:alternate}.HomePage .app-name{font-size:3rem;margin:30px auto;text-align:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}
